function buildCA()
{
    for (let i = 0; i < xCellWidth; i++)
    {
        state[i] = []
        nextState[i] = []
        for (let j = 0; j < zCellWidth; j++)
        {
            state[i][j] = false
        }
    }
    // set glider true states here
    //state[xCellWidth / 2][xCellWidth / 2] = true
    //state[xCellWidth / 2 - 2][xCellWidth / 2] = true
    //state[xCellWidth / 2 + 1][xCellWidth / 2] = true
    //state[xCellWidth / 2 + 1][xCellWidth / 2 - 1] = true
    //state[xCellWidth / 2][xCellWidth / 2 - 2] = true

    //state[5][5] = true
    //state[5 - 2][5] = true
    //state[5 + 1][5] = true
    //state[5 + 1][5 - 1] = true
    //state[5][5 - 2] = true

    const midXY = xCellWidth / 2 ;

    glider(state, midXY -10, midXY - 25) // top
    glider(state, midXY +10, midXY - 25) //top right

    glider(state, midXY - 10, midXY + 25) // bottom
    glider(state, midXY + 10, midXY + 25) //bottom right    
   
    //left
    glider_b(state, midXY - 20, midXY )
    glider_b(state, midXY - 20, midXY - 5 )

    glider_b(state, midXY-15, midXY )
    glider_b(state, midXY - 15, midXY - 5)


    ////bottom
    //glider_b(state, midXY + 20, midXY)
    //glider_b(state, midXY + 20, midXY - 5)

    //glider_b(state, midXY + 15, midXY)
    //glider_b(state, midXY + 15, midXY - 5)



    glider_b(state, midXY + 20, midXY - 20)
    glider_b(state, midXY + 20, midXY - 5 - 20)

    glider_b(state, midXY + 15, midXY -20)
    glider_b(state, midXY + 15, midXY - 5 - 20)

  


    while (count < maxLevel)
    {
        for (let i = 0; i < xCellWidth; i++)
        {
            for (let j = 0; j < zCellWidth; j++)
            {

                // 1 - register true states position
                if (state[i][j] === true)
                {

                    //bring it to center by adding the width/2
                    const newi = i + -xCellWidth/2
                    const newj = j + -zCellWidth/2

                    const pos =
                    {
                        x: newi*0.8,
                        y: count * 0.2,
                        z: newj * 0.8
                    }
                    caPosArray.push(pos)

                    caPosAttribute.push(pos.x)
                    caPosAttribute.push(pos.y)
                    caPosAttribute.push(pos.z)
                }

                let aliveCells = 0

                // 2 - check neighbour
                for (let m = -1; m <= 1; m++)
                {
                    for (let k = -1; k <= 1; k++)
                    {
                        if (!(m === 0 & k === 0))
                        {
                            if (state[(m + i + xCellWidth) % xCellWidth][(k + j + zCellWidth) % zCellWidth] === true)
                            {
                                aliveCells++
                            }
                        }
                    }
                }

                // 3 - rules
                if (aliveCells < 2 || aliveCells > 3) {
                    nextState[i][j] = false
                }
                else if (aliveCells === 3 )//|| aliveCells === 1)
                {
                   nextState[i][j]=true

                }
                else
                {
                    nextState[i][j] = state[i][j]
                }

                

            }
        }
        

        for (let i = 0; i < xCellWidth; i++)
        {
            for (let j = 0; j < zCellWidth; j++)
            {
                state[i][j] = nextState[i][j]

            }
        }

        count++

    }  

}

//buildCA()
//console.log('states = ' + state)
//console.log(caPosArray)
//console.log(caPosAttribute)


function glider(state, x, y)
{
    
    state[x - 2][y + 1] = true
    state[x - 2][y] = true
    state[x - 2][y - 1] = true
    state[x - 1][y + 2] = true
    state[x - 1][y - 2] = true
    state[x][y + 3] = true
    state[x][y - 3] = true
    state[x + 1][y + 2] = true
    state[x + 1][y - 2] = true
    state[x + 2][y + 1] = true
    state[x + 2][y] = true
    state[x + 2][y - 1] = true
    state[x + 3][y + 1] = true
    state[x + 3][y] = true
    state[x + 3][y - 1] = true

}

function glider_b(state, x, y)
{
    
    state[x-2][y] = true
    state[x-2][y+1] = true
    state[x-1][y-1] = true
    state[x-1][y+2] = true
    state[x][y-1] = true
    state[x][y+2] = true
    state[x+1][y-1] = true
    state[x+1][y+2] = true
    state[x+2][y] = true
    state[x+2][y+1] = true
}